
.launch {
  font-family: monospace;
}
.launch > .rd-swipe {
  width: 100%;
}
.launch > .intro {
  padding: 30px 0;
}
.launch > .intro > ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  list-style: none;
}
.launch > .intro > ul > li {
  padding: 0 6%;
  width: 12%;
}
.launch > .intro > ul > li > img {
  width: 100%;
}
.launch > .intro > ul > li > h3 {
  margin-top: 10px;
  text-align: center;
}
.launch > .intro > ul > li > h3,
.launch > .intro > ul > li > p {
  font-weight: bolder;
  font-size: 20px;
}
.launch > .intro > .login {
  margin-top: 3%;
  text-align: center;
}
.launch > .intro > .login > .wrap {
  position: relative;
  margin: auto;
  padding: 30px 0;
  width: 52%;
  height: 400px;
  background: url('../assets/login.jpeg') no-repeat center center;
  background-size: cover;
}
.launch > .intro > .login > .wrap > h3 {
  color: #fff;
  font-size: 300%;
}
.launch > .intro > .login > .wrap > input {
  display: block;
  margin: 30px auto;
  width: 85%;
  height: 40px;
  text-align: center;
  font-size: 200%;
}
.launch > .intro > .login > .wrap ::-webkit-input-placeholder {
  /* WebKit, Blink, Edge */
  color: #a568d0;
}
.launch > .intro > .login > .wrap :-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #a568d0;
}
.launch > .intro > .login > .wrap ::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #a568d0;
}
.launch > .intro > .login > .wrap :-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #a568d0;
}
.launch > .intro > .login > .wrap > p {
  position: absolute;
  bottom: 40px;
  width: 100%;
  color: #fff;
  font-weight: bolder;
  font-size: 350%;
}
.launch > .contact {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #edeff2;
  padding: 5%;
  list-style: none;
}
.launch > .contact > li {
  -webkit-box-flex: 2;
      -ms-flex: 2;
          flex: 2;
}
.launch > .contact > li > h3 {
  margin-bottom: 15px;
  font-size: 30px;
}
.launch > .contact > li > p {
  font-size: 18px;
}
.launch > .contact > .contact > .wrap > dl {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 10px 0;
  width: 75%;
  border-bottom: 1px solid #9f9fa0;
}
.launch > .contact > .contact > .wrap > dl > dt {
  width: 42px;
  height: 42px;
  padding: 0 10px;
}
.launch > .contact > .contact > .wrap > dl > dt > .fa-icon {
  width: 100%;
  height: 100%;
}
.launch > .contact > .contact > .wrap > dl > dd {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.launch > .contact > .contact > .wrap > dl > dd > p {
  font-size: 18px;
}
.launch > .contact > .follow {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.launch > .contact > .follow > img {
  margin: 5px;
}



