
.footer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 10px;
  background: #7f7f7f;
  color: #fff;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.footer > .text-wrap {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.footer > .text-wrap > p {
  margin-bottom: 10px;
  font-size: 14px;
}
.footer > .text-wrap > .links > a {
  display: inline-block;
  padding: 0 5px;
  color: #fff;
  text-decoration: none;
  font-size: 12px;
}
.footer > .text-wrap > .links > a + a {
  border-left: 1px solid #fff;
}
.footer > .up-wrap {
  cursor: pointer;
}
.footer > .up-wrap > .fa-icon {
  width: 40px;
  height: 40px;
}



