
.sign {
  background: url('../assets/sign.jpeg') center no-repeat;
  background-size: cover;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-family: monospace;
}
.sign > .widget {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 80px 0 20px 0px;
  width: 80%;
  opacity: 0.8;
  background: #ccc;
}
.sign > .widget > form {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin: 0px 100px 0px 50px;
}
.sign > .widget > form > .input-group {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 10px 0px;
}
.sign > .widget > form > .input-group > span {
  width: 180px;
  font-size: 20px;
  font-weight: bolder;
}
.sign > .widget > form > .input-group > input,
.sign > .widget > form > .input-group > .select-wrap {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  border: 2px solid #000;
}
.sign > .widget > form > .input-group > input,
.sign > .widget > form > .input-group select {
  padding: 5px;
}
.sign > .widget > form > .input-group > .select-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.sign > .widget > form > .input-group > .select-wrap > select {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 100%;
  border-radius: 0 !important;
  -webkit-appearance: none;
  -webkit-border-radius: 0px;
}
.sign > .widget > form > .input-group > .select-wrap .fa-icon {
  width: 20px;
  height: 27px;
}
.sign > .widget > form > p {
  line-height: 15px;
