.sign > .widget > .contact {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 0 50px 0 50px;
  border-left: 1px solid #000;
}
.sign > .widget > .contact > .title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.sign > .widget > .contact > .title > img {
  width: 72px;
}
.sign > .widget > .contact > .title > p {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
